"use client";

import { useState } from "react";
import { motion } from "framer-motion";
import { useRouter } from "next/navigation";
import useCampaignStore from "@/store/campaignStore";
import useAuthStore from "@/store/authStore";
import toast from "react-hot-toast";

export default function FundraiserForm() {
  const router = useRouter();
  const { createCampaign, isLoading } = useCampaignStore();
  const { isAuthenticated } = useAuthStore();
  
  const [formData, setFormData] = useState({
    title: "",
    shortDescription: "",
    category: "medical",
    goalAmount: "",
    beneficiaryName: "",
    beneficiaryRelationship: "",
    beneficiaryContact: "",
    city: "",
    state: "",
    address: "",
    startDate: "",
    endDate: "",
    story: "",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    // Check authentication
    if (!isAuthenticated) {
      toast.error("Please login to start a fundraiser");
      router.push("/login");
      return;
    }

    try {
      // Validate required fields
      if (!formData.story || formData.story.trim().length < 50) {
        toast.error("Description must be at least 50 characters long");
        return;
      }

      if (!formData.beneficiaryRelationship || formData.beneficiaryRelationship.trim().length < 2) {
        toast.error("Please specify your relationship with the beneficiary (e.g., Self, Son, Daughter, Friend)");
        return;
      }

      const campaignData = {
        title: formData.title.trim(),
        shortDescription: formData.shortDescription.trim(),
        description: formData.story.trim(), // Backend expects 'description', not 'fullDescription'
        category: formData.category,
        goalAmount: parseFloat(formData.goalAmount),
        beneficiary: {
          name: formData.beneficiaryName.trim(),
          relationship: formData.beneficiaryRelationship.trim(), // Required by backend validation
          // Contact should be an object with phone and/or email, not a string
          contact: (() => {
            const contactValue = formData.beneficiaryContact.trim();
            const isEmail = contactValue.includes('@');
            return {
              ...(isEmail ? { email: contactValue } : { phone: contactValue })
            };
          })()
        },
        location: {
          city: formData.city.trim(),
          state: formData.state.trim(),
          address: formData.address.trim(),
        },
        timeline: {
          // Ensure dates are in ISO format
          startDate: formData.startDate 
            ? new Date(formData.startDate).toISOString() 
            : new Date().toISOString(),
          endDate: formData.endDate 
            ? new Date(formData.endDate).toISOString() 
            : new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString(), // Default 30 days from now
          // Calculate duration in days
          duration: formData.endDate && formData.startDate 
            ? Math.ceil((new Date(formData.endDate) - new Date(formData.startDate || new Date())) / (1000 * 60 * 60 * 24))
            : 30
        }
      };

      // Debug: Log campaign data before submission
      console.log("Submitting campaign data:", campaignData);
      console.log("Title length:", campaignData.title.length);
      console.log("Description length:", campaignData.description.length);
      console.log("Short description length:", campaignData.shortDescription.length);
      console.log("Category:", campaignData.category);
      console.log("Beneficiary relationship:", campaignData.beneficiary.relationship);

      const response = await createCampaign(campaignData);
      console.log("Campaign created successfully:", response);
      
      toast.success("Fundraiser created successfully! Waiting for admin approval.");
      
      // Reset form
      setFormData({
        title: "",
        shortDescription: "",
        category: "medical",
        goalAmount: "",
        beneficiaryName: "",
        beneficiaryRelationship: "",
        beneficiaryContact: "",
        city: "",
        state: "",
        address: "",
        startDate: "",
        endDate: "",
        story: "",
      });

      // Redirect to dashboard immediately
      router.push("/dashboard");
      
    } catch (error) {
      console.error("Campaign creation error:", error);
      console.error("Error response:", error.response?.data);
      
      // Show detailed error message
      const errorData = error.response?.data || error;
      const errorMessage = errorData?.message || error.message || "Failed to create fundraiser";
      const errorDetails = errorData?.errors;
      
      if (errorDetails && Array.isArray(errorDetails) && errorDetails.length > 0) {
        // Show all validation errors
        const errorMessages = errorDetails.map(err => {
          if (typeof err === 'string') return err;
          if (err.msg) return err.msg;
          if (err.message) return err.message;
          return JSON.stringify(err);
        });
        toast.error(errorMessages.join(", "), { duration: 5000 });
        console.error("Validation errors:", errorMessages);
      } else {
        toast.error(errorMessage, { duration: 5000 });
      }
    }
  };

  return (
    <section className="relative bg-gray-50 overflow-hidden">
      
      <div className="relative min-h-[60vh] flex flex-col items-center justify-center p-8 text-center">
        <div className="absolute inset-0">
          <img
            src="/images/main-slider/123.jpg"
            alt="Fundraiser background"
            className="object-cover w-full h-full scale-105 opacity-60 animate-slow-zoom"
          />
          
          <div className="absolute inset-0 bg-gradient-to-r from-black/70 via-black/50 to-transparent" />
        </div>

        <motion.h2
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 1 }}
          className="relative z-10 text-4xl md:text-5xl font-extrabold text-white drop-shadow-lg"
        >
          Start your fundraiser and make a difference today!
        </motion.h2>

        <motion.p
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.5, duration: 1 }}
          className="relative z-10 mt-4 text-lg text-gray-200 max-w-2xl"
        >
          Empower change ‚Äî raise funds for causes that matter. Let‚Äôs bring your
          story to life.
        </motion.p>
      </div>

      <div className="max-w-5xl mx-auto px-6 py-16">
        <motion.form
          onSubmit={handleSubmit}
          initial={{ opacity: 0, y: 40 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
          className="bg-white/90 backdrop-blur-xl p-8 rounded-2xl shadow-2xl border border-gray-200 space-y-6"
        >
          
          <input
            type="text"
            name="title"
            placeholder="Fundraiser Title"
            value={formData.title}
            onChange={handleChange}
            required
            className="border rounded-lg p-3 w-full text-gray-700 focus:ring-2 focus:ring-green-400 outline-none transition"
          />

          <textarea
            name="shortDescription"
            placeholder="Short Description (max 200 characters)"
            value={formData.shortDescription}
            onChange={handleChange}
            maxLength="200"
            required
            rows="2"
            className="border rounded-lg p-3 w-full text-gray-700 focus:ring-2 focus:ring-green-400 outline-none transition"
          />

          <div className="grid md:grid-cols-2 gap-4">
            <select
              name="category"
              value={formData.category}
              onChange={handleChange}
              required
              className="border rounded-lg p-3 w-full text-gray-700 focus:ring-2 focus:ring-green-400 outline-none transition"
            >
              <option value="medical">Medical</option>
              <option value="education">Education</option>
              <option value="disaster">Disaster</option>
              <option value="animal">Animal</option>
              <option value="environment">Environment</option>
              <option value="sports">Sports</option>
              <option value="technology">Technology</option>
              <option value="other">Other</option>
            </select>

            <input
              type="number"
              name="goalAmount"
              placeholder="Goal Amount (INR)"
              value={formData.goalAmount}
              onChange={handleChange}
              required
              min="1000"
              className="border rounded-lg p-3 w-full text-gray-700 focus:ring-2 focus:ring-green-400 outline-none transition"
            />
          </div>

          <div>
            <label className="block font-medium mb-2 text-gray-700">Upload Pictures</label>
            <div className="grid md:grid-cols-3 gap-3">
              {[1, 2, 3].map((i) => (
                <input
                  key={i}
                  type="file"
                  className="border p-2 rounded-lg w-full text-gray-600 hover:border-green-400 cursor-pointer transition"
                />
              ))}
            </div>
          </div>

          <div className="grid md:grid-cols-2 gap-4">
            <input
              type="text"
              name="beneficiaryName"
              placeholder="Beneficiary Name"
              value={formData.beneficiaryName}
              onChange={handleChange}
              required
              className="border rounded-lg p-3 w-full text-gray-700 focus:ring-2 focus:ring-green-400 outline-none transition"
            />
            <input
              type="text"
              name="beneficiaryRelationship"
              placeholder="Your Relationship (e.g., Self, Son, Daughter, Friend)"
              value={formData.beneficiaryRelationship}
              onChange={handleChange}
              required
              minLength={2}
              maxLength={50}
              className="border rounded-lg p-3 w-full text-gray-700 focus:ring-2 focus:ring-green-400 outline-none transition"
            />
          </div>

          <input
            type="text"
            name="beneficiaryContact"
            placeholder="Beneficiary Contact (Phone/Email)"
            value={formData.beneficiaryContact}
            onChange={handleChange}
            required
            className="border rounded-lg p-3 w-full text-gray-700 focus:ring-2 focus:ring-green-400 outline-none transition"
          />

          <div className="grid md:grid-cols-2 gap-4">
            <input
              type="text"
              name="city"
              placeholder="City"
              value={formData.city}
              onChange={handleChange}
              required
              className="border rounded-lg p-3 w-full text-gray-700 focus:ring-2 focus:ring-green-400 outline-none transition"
            />
            <input
              type="text"
              name="state"
              placeholder="State"
              value={formData.state}
              onChange={handleChange}
              required
              className="border rounded-lg p-3 w-full text-gray-700 focus:ring-2 focus:ring-green-400 outline-none transition"
            />
          </div>

          <textarea
            name="address"
            placeholder="Full Address"
            rows={2}
            value={formData.address}
            onChange={handleChange}
            required
            className="border rounded-lg p-3 w-full text-gray-700 focus:ring-2 focus:ring-green-400 outline-none transition"
          />

          <div className="grid md:grid-cols-2 gap-4">
            <div>
              <label className="block font-medium mb-1 text-gray-700">Aadhar Card</label>
              <input
                type="file"
                className="border p-2 rounded-lg w-full text-gray-600 hover:border-green-400 cursor-pointer transition"
              />
            </div>
            <div>
              <label className="block font-medium mb-1 text-gray-700">PAN Card</label>
              <input
                type="file"
                className="border p-2 rounded-lg w-full text-gray-600 hover:border-green-400 cursor-pointer transition"
              />
            </div>
          </div>

          <div className="grid md:grid-cols-2 gap-4">
            <div>
              <label className="block font-medium mb-1 text-gray-700">Start Date</label>
              <input
                type="date"
                name="startDate"
                value={formData.startDate}
                onChange={handleChange}
                required
                className="border rounded-lg p-3 w-full text-gray-700 focus:ring-2 focus:ring-green-400 outline-none transition"
              />
            </div>
            <div>
              <label className="block font-medium mb-1 text-gray-700">End Date</label>
              <input
                type="date"
                name="endDate"
                value={formData.endDate}
                onChange={handleChange}
                required
                className="border rounded-lg p-3 w-full text-gray-700 focus:ring-2 focus:ring-green-400 outline-none transition"
              />
            </div>
          </div>

          <div>
            <label className="block font-medium mb-2 text-gray-700">Upload Supporting Documents</label>
            <div className="grid md:grid-cols-2 gap-3">
              {[1, 2].map((i) => (
                <input
                  key={i}
                  type="file"
                  className="border p-2 rounded-lg w-full text-gray-600 hover:border-green-400 cursor-pointer transition"
                />
              ))}
            </div>
          </div>

          <div>
            <label className="block font-medium mb-2 text-gray-700">Upload Video</label>
            <input
              type="file"
              className="border p-2 rounded-lg w-full text-gray-600 hover:border-green-400 cursor-pointer transition"
            />
          </div>

          <textarea
            name="story"
            placeholder="Full Story - Explain why you need funds and how they will be used (Minimum 50 characters)"
            rows={6}
            value={formData.story}
            onChange={handleChange}
            required
            minLength={50}
            maxLength={5000}
            className="border rounded-lg p-3 w-full text-gray-700 focus:ring-2 focus:ring-green-400 outline-none transition"
          />
          {formData.story && (
            <p className="text-sm text-gray-500">
              {formData.story.length}/5000 characters 
              {formData.story.length < 50 && (
                <span className="text-red-500"> (Minimum 50 characters required)</span>
              )}
            </p>
          )}

          <div className="text-center pt-4">
            <motion.button
              type="submit"
              disabled={isLoading}
              whileHover={{ scale: isLoading ? 1 : 1.05 }}
              whileTap={{ scale: isLoading ? 1 : 0.98 }}
              className={`px-8 py-3 rounded-lg font-medium transition shadow-lg ${
                isLoading 
                  ? 'bg-gray-400 cursor-not-allowed' 
                  : 'bg-green-600 hover:bg-green-700 text-white'
              }`}
            >
              {isLoading ? 'Submitting...' : 'Submit Fundraiser'}
            </motion.button>
          </div>
        </motion.form>
      </div>
    </section>
  );
}


